@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
body{
    background:#FFFDD0;
}

div{
    padding:0;
    box-sizing: border-box;
    background: orange;
    display:flex;
    justify-content:center;
    align-items:center;
    flex-direction: column;
    border-radius: 12px;
    margin: 60px 100px;
}

li{
    font-family: 'Montserrat', sans-serif;
    font-weight: bold;
    font-size:10vh;
    margin: 10px;
    color: #dc2020;
}

span{
    color: rgb(31, 41, 80);
}
